
.tued-ext-contenteditable-element {
    display: inline-block;
    outline: none !important;

    &:hover {
        outline: 1px dotted #8c8c8c !important;
    }

    &:empty:before {
        content: attr(data-placeholder);
        color: rgba(0,0,0,.3);
        font-style: italic;
        display: inline-block;
    }
}
