
.tued-ext-font-icon-list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -5px;
    margin-right: -5px;

    .tued-ext-font-icon-list-item {
        flex: 1 1 100px;
        padding: 5px;
        display: flex;

        .tued-ext-font-icon-list-item-inner {
            flex: 1;
            background-color: #f3f3f3;
            padding: 4px;
            border-radius: 3px;
            text-align: center;
            font-size: 31px;
            max-width: 100%;

            span {
                display: block;
                font-size: 12px;
                word-wrap: break-word;
            }

            &:hover {
                background-color: #e0e0e0;
                cursor: pointer;
            }
        }
    }
}
